<template>
  <div id="app">
    <button @click="authorize">AUTH</button>
    <button @click="fetchVideos">FETCH</button>
    <br>
    <button @click="prevFrame">PF</button>
    <button @click="nextFrame">NF</button>
    <br>
    <br>
    <span>BLUR</span><input type="range" min="0" max="20" step="0.25" v-model="blur">
    <span>PBR</span><input type="range" max="1" min="0" step="0.05" v-model="playbackRate">
    <br>
    <br>
    <span>BR</span><input type="range" max="10" min="0" step="0.05" v-model="brightness">
    <span>SD</span><input type="range" max="1" min="0" step="0.05" v-model="scaleDown">
    <video
      controls
      :src="url"
      ref="video"
      v-if="url"
      :playbackRate.prop="playbackRate"
      :style="styleObj"
    ></video>
    <div v-if="videos !== null">
      <div class="video-item" v-for="video in videos">
        <p>{{ video.name }}</p>
        <img :src="video.thumbnailLink">
        <button @click="watch(video)">watch</button>
        <button @click="download(video)">download</button>
        <hr>
      </div>
    </div>
    <iframe :src="iframeUrl" style="display:none"></iframe>
  </div>
</template>

<script>
const ffoutputFolderId = '10Eq2Pabv_UARecpLEQUusz6nmS-3Fct7';

export default {
  data() {
    return {
      videos: null,
      url: null,
      iframeUrl: null,
      blur: 0,
      brightness: 1,
      playbackRate: 1,
      scaleDown: 1
    }
  },
  computed: {
    styleObj() {
      return {
        filter: `blur(${this.blur}px) brightness(${this.brightness})`,
        transform: `scale(${this.scaleDown})`
      }
    }
  },
  methods: {
    watch(video) {
      this.url = `https://www.googleapis.com/drive/v3/files/${video.id}?alt=media`;
    },
    download(video) {
      window.open(video.webContentLink)
    },
    fetchVideos() {
      gapi.client.drive.files.list({
        q: `"${ffoutputFolderId}" in parents`,
        fields: 'files(id, name, webContentLink, hasThumbnail, thumbnailLink, videoMediaMetadata)',
        pageSize: 1000
      }).then(res => {
        res = JSON.parse(res.body);
        this.videos = res.files;
      });
      
      const token = gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().access_token;

      navigator.serviceWorker.ready.then(registration => {
        registration.active.postMessage(token)
      })
    },
    prevFrame() {
      this.$refs.video.pause();
      this.$refs.video.currentTime -= 1 / 24;
    },
    nextFrame() {
      this.$refs.video.pause();
      this.$refs.video.currentTime += 1 / 24;
    },
    authorize() {
      gapi.auth2.getAuthInstance().signIn();
    },
    handleClientLoad() {
      gapi.load('client:auth2', this.initClient);
    },
    initClient() {
      gapi.client.init({
        apiKey: 'AIzaSyBf30HVghnNBiz9JeqlMSAX0wPOo9KnZJs',
        clientId: '271375874139-d2r4cgevop6vugqso2osv3dg17u2ib18.apps.googleusercontent.com',
        discoveryDocs: ['https://www.googleapis.com/discovery/v1/apis/drive/v3/rest'],
        scope: 'https://www.googleapis.com/auth/drive'
      });
    },
    loadGoogleApisScript() {
      const script = document.createElement('script');
      script.async = true;
      script.defer = true;
      script.src = 'https://apis.google.com/js/api.js';
      script.onload = () => {
        this.handleClientLoad();
      };
      document.body.appendChild(script);
    }
  },
  created() {
    this.loadGoogleApisScript();
    document.addEventListener('keyup', e => {
      if (e.key === ',') {
        this.prevFrame()
      } else if (e.key === '.') {
        this.nextFrame()
      }
    })
  }
}
</script>

<style lang="scss">
body {
  touch-action: manipulation;
  margin: 0;
  font-family: monospace;
}

video {
  width: 375px;
}

button {
  width: 100px;
  height: 100px;
  font-size: 30px;
}
span {
  font-size: 20px;
}
video {
  transform-origin: top
}
</style>
